.NavBar{
    background-color: black;
    color: white;
    position: fixed;
    width: 100vw;
    max-height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: left;
}
.title{
    margin-top: 1vh;
    margin-bottom: 1vh;
    font-size: 3vw;
    align-items: left;
    margin-left: 5vw;
}
.Navlinks{
    margin-left: 5%;
    margin-top: 20px;
    margin-bottom: 15px;
    width:70vw;
}
.NavItem{
    margin-top: 20px;
    margin-bottom: 15px;
    color: gray;
    width: 30px;
    text-decoration: none;
    font-size: 36px;
    margin-left: 5%;
}
.NavItem.active{
    color: white;
}
.NavItem:hover{
    color: white;
}